@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);
.Nav.NavBar{flex-shrink:0;margin:0;height:80px;font-size:22px;font-weight:bold;border-radius:0}.Nav.NavBar .Nav-Item>.Nav-Logo[alt="Sikorsky Airport"]{width:150px;height:auto}

.HomePage{display:flex;justify-content:center;align-items:center;flex-grow:1;width:100%;text-align:center}.HomePage-Wrapper.segment{width:64%;background-color:rgba(255,255,255,0.5);border:none;box-shadow:0 0 30px 30px rgba(255,255,255,0.5)}.HomePage-Text.header{line-height:1.8;margin-top:2em}

.DirectionBtn{width:100%;display:flex;color:#fff}.DirectionBtn-AirBtn{padding:10px;font-size:22px;line-height:60px;text-transform:uppercase;width:100%;border:none;outline:none;border-radius:60px 60px 0 0;transition:all 0.3s}.DirectionBtn-AirBtn_focus{color:#2185d0;background-color:#f9fafb;z-index:1}.DirectionBtn-AirBtn_focusNo{color:#fff;background-color:#2185d0;opacity:0.5}.DirectionBtn-AirBtn_focusNo:hover{opacity:1}.DirectionBtn-AirBtn_departures{margin-right:-80px}.DirectionBtn-AirBtn_arrivals{margin-left:-80px}.DirectionBtn-Svg{margin-right:15px;width:40px;height:28px;transform:translateY(5px)}@media (max-width: 995px){.DirectionBtn-AirBtn{font-size:16px}.DirectionBtn-AirBtn_departures{margin-right:-50px}.DirectionBtn-AirBtn_arrivals{margin-left:-50px}.DirectionBtn-Svg{width:30px;height:21px}}@media (max-width: 767px){.DirectionBtn-AirBtn{padding:0;font-size:14px}.DirectionBtn-AirBtn_departures{margin-right:-20px;padding-left:15px;text-align:left;border-radius:0 25px 0 0}.DirectionBtn-AirBtn_arrivals{margin-left:-20px;padding-right:15px;text-align:right;border-radius:25px 0 0}}

.FlightsTable-TableRow.Flight{transition:box-shadow 200ms ease-out}.FlightsTable-TableRow.Flight:nth-child(2n){background-color:rgba(0,102,135,0.1)}.FlightsTable-TableRow.Flight:hover{box-shadow:0 5px 10px rgba(0,0,0,0.2)}.FlightsTable-TableRow.Flight .Flight-TermCircle{display:flex;justify-content:center;align-items:center;margin:auto;width:40px;height:40px;color:#63c745;font-size:20px;font-weight:700;border-radius:50%;border:2px solid #63c745}.FlightsTable-TableRow.Flight .Flight-TermCircle_d{color:#1eb7ee;border-color:#1eb7ee}.FlightsTable-TableRow.Flight .Flight-AirlineImage{margin-right:10px}.FlightsTable-TableRow.Flight .Flight-AirlineName{display:inline-block;vertical-align:middle}.FlightsTable-TableRow.Flight .Flight-Details{text-align:center;text-overflow:clip !important}.FlightsTable-TableRow.Flight .Flight-DetailsButton.basic.button{width:100%;line-height:1.2}.FlightsTable-TableRow.Flight .Flight-DetailsButton.basic.button:hover{color:#fff !important;background-color:#2185d0 !important}.FlightsTable-TableRow.Flight .Flight-DetailsIcon.icon{margin-right:12px !important}@media (max-width: 995px){.FlightsTable-TableRow.Flight .Flight-DetailsIcon.icon{margin-right:0 !important}.FlightsTable-TableRow.Flight .Flight-DetailsText{display:none}}@media (max-width: 767px){.FlightsTable-TableRow.Flight[class*="center aligned"]:hover{box-shadow:0 5px 10px rgba(0,0,0,0.2) !important}.FlightsTable-TableRow.Flight .Flight-DetailsButton.basic.button{color:#fff !important;background-color:#2185d0 !important}.FlightsTable-TableRow.Flight .Flight-DetailsButton.basic.button:hover{color:#2185d0 !important;background-color:#fff !important}.FlightsTable-TableRow.Flight .Flight-DetailsIcon.icon{margin-right:15px !important}.FlightsTable-TableRow.Flight .Flight-DetailsText{display:inline;font-size:15px}}

.FlightsTable.ui.table{margin:0;border-radius:0}.FlightsTable-TableHeader .FlightsTable-TableRow .FlightsTable-HeaderCell{color:#2185d0;background-color:rgba(0,102,135,0.1)}.FlightsTable-TableHeader .FlightsTable-TableRow .FlightsTable-HeaderCell_gate{width:70px}.FlightsTable-TableHeader .FlightsTable-TableRow .FlightsTable-HeaderCell_terminal,.FlightsTable-TableHeader .FlightsTable-TableRow .FlightsTable-HeaderCell_time,.FlightsTable-TableHeader .FlightsTable-TableRow .FlightsTable-HeaderCell_flight{width:92px}@media (max-width: 767px){.FlightsTable-TableHeader{background-color:rgba(0,102,135,0.1)}}

.FlightsPage{display:flex;flex-direction:column;align-items:center;flex-grow:1;width:100%;text-align:center;margin-bottom:30px}.FlightsPage-Header.ui.header{margin-top:2rem}.FlightsPage-Error.header{margin-top:0}.FlightsPage-Tabs.ui.attached.menu{width:100%;background-color:transparent;border:none}.FlightsPage-Tabs.ui.attached.menu .FlightsPage-Link{flex:1 1 auto;justify-content:center;padding:22px;text-transform:uppercase;background-color:rgba(33,133,208,0.5);color:#fff;border-radius:0}.FlightsPage-Tabs.ui.attached.menu .FlightsPage-Link:hover{background-color:#2185d0}.FlightsPage-Tabs.ui.attached.menu .FlightsPage-Link_active{background-color:#f9fafb;color:#2185d0}.FlightsPage-Tabs.ui.attached.menu .FlightsPage-Link_active:hover{background-color:#f9fafb}.FlightsPage-Tabs.ui.attached.menu .FlightsPage-Link:last-child:before{display:none}.FlightsPage-TabPane.ui.segment.tab{border:none;padding:0}@media (max-width: 767px){.FlightsPage-Tabs.ui.attached.menu .FlightsPage-Link{padding:15px}}

.Spinner{display:flex;flex-direction:column;justify-content:center;align-items:center;flex-grow:1;perspective:62.5rem}.Spinner-TextInfo{color:#2185d0}.Spinner-D{position:relative;top:0;-webkit-animation:letter 6s infinite ease-in;animation:letter 6s infinite ease-in}.Spinner-Circle,.Spinner-Line{width:12.5rem;height:12.5rem;border:3px solid #2185d0;border-radius:50%}.Spinner-Circle{position:relative;transform-style:preserve-3d;-webkit-animation:spinn 6s infinite linear;animation:spinn 6s infinite linear}.Spinner-Line{position:absolute;display:block;top:-5px;right:0;bottom:0;left:-5px}.Spinner-Line:nth-child(1){transform:rotateY(36deg)}.Spinner-Line:nth-child(2){transform:rotateY(72deg)}.Spinner-Line:nth-child(3){transform:rotateY(108deg)}.Spinner-Line:nth-child(4){transform:rotateY(144deg)}@-webkit-keyframes letter{100%{top:1.25rem}}@keyframes letter{100%{top:1.25rem}}@-webkit-keyframes spinn{0%{transform:rotateX(180deg) rotateY(0deg) rotateZ(0deg)}50%{transform:rotateX(360deg) rotateY(160deg) rotateZ(200deg)}100%{transform:rotateX(0deg) rotateY(360deg) rotateZ(360deg)}}@keyframes spinn{0%{transform:rotateX(180deg) rotateY(0deg) rotateZ(0deg)}50%{transform:rotateX(360deg) rotateY(160deg) rotateZ(200deg)}100%{transform:rotateX(0deg) rotateY(360deg) rotateZ(360deg)}}

.FlightDetails{width:100%;margin-bottom:30px}.FlightDetails-Header.ui.header{margin-top:2rem;text-align:center}.FlightDetails-BackBtn{padding:4px 0 0 10px;margin-bottom:10px;font-size:22px;font-weight:bold;line-height:55px;width:110px;border:none;outline:none;border-radius:50px 0 0;transition:all 0.3s;text-align:center;color:#2185d0;background-color:#f9fafb;box-shadow:0 3px 6px rgba(0,0,0,0.16)}.FlightDetails-BackBtn:hover{color:#fff;background-color:#2185d0}.FlightDetails-Flight,.FlightDetails-Destination{display:flex;align-items:flex-start;margin-top:10px;font-family:"Open Sans", sans-serif}.FlightDetails-FlightCode.ui.header,.FlightDetails-FlightFlies.ui.header,.FlightDetails-DestinationCode.ui.header,.FlightDetails-DestinationFlies.ui.header{margin:0;font-family:inherit}.FlightDetails-FlightFlies.ui.header,.FlightDetails-DestinationFlies.ui.header{margin-left:15px;font-weight:300}.FlightDetails-FlightHeader.ui.header,.FlightDetails-DestinationHeader.ui.header{margin:0;font-size:70px;font-family:inherit;font-weight:300;line-height:1}.FlightDetails-FlightImage,.FlightDetails-DestinationImage{margin-left:30px}.FlightDetails-Status.ui.header{margin-top:10px}.FlightDetails-Table.ui.table{margin-top:30px;background-color:rgba(231,239,236,0.9);box-shadow:0 0 30px 30px rgba(231,239,236,0.9)}.FlightDetails-Table.ui.table .FlightDetails-HeaderCell{color:#888}.FlightDetails-Table.ui.table .FlightDetails-TableBody .FlightDetails-TableRow{font-family:"Open Sans", sans-serif;font-size:22px;font-weight:700}.FlightDetails-Airline.ui.accordion{margin-top:30px}.FlightDetails-Airline.ui.accordion .FlightDetails-AirlineName{display:flex;align-items:center;font-size:25px}@media (max-width: 767px){.FlightDetails{padding-left:30px}.FlightDetails-FlightFlies.ui.header,.FlightDetails-DestinationFlies.ui.header{margin-left:7px;font-size:18px;font-weight:400}.FlightDetails-FlightCode.ui.header,.FlightDetails-DestinationCode.ui.header{font-size:18px;font-weight:normal;color:#888}.FlightDetails-FlightHeader.ui.header,.FlightDetails-DestinationHeader.ui.header{font-size:50px}.FlightDetails-FlightImage.ui.image,.FlightDetails-DestinationImage.ui.image{margin-left:10px;width:100px}.FlightDetails-Status.ui.header{margin-top:10px;font-size:23px}.FlightDetails-Table.ui.table{margin-top:0}.FlightDetails-Table.ui.table .FlightDetails-TableBody .FlightDetails-TableRow{font-size:18px}.FlightDetails-Airline.ui.accordion{margin-top:20px}.FlightDetails-Airline.ui.accordion .ui.big.message{font-size:16px}.FlightDetails-Airline.ui.accordion .FlightDetails-DestinationImage{width:70px}}

.Main{display:flex;justify-content:center;flex-grow:1;width:100%;padding:0 3rem}@media (max-width: 767px){.Main{padding:0 1em}}

.Hero{position:absolute;top:0;left:0;width:100%;height:100%;overflow:hidden;z-index:-1}.Cube{position:absolute;top:72vh;left:48vw;width:10px;height:10px;border:solid 1px #3a99e0;border-radius:1px;transform-origin:top left;transform:scale(0) rotate(0deg) translate(-50%, -50%);-webkit-animation:cube 10.5s ease-in forwards infinite;animation:cube 10.5s ease-in forwards infinite}.Cube:nth-child(2n){border-color:#207fc7}.Cube:nth-child(2){-webkit-animation-delay:1.5s;animation-delay:1.5s;left:25vw;top:40vh}.Cube:nth-child(3){-webkit-animation-delay:3s;animation-delay:3s;left:90vw;top:65vh}.Cube:nth-child(4){-webkit-animation-delay:4.5s;animation-delay:4.5s;left:70vw;top:25vh}.Cube:nth-child(5){-webkit-animation-delay:6s;animation-delay:6s;left:10vw;top:85vh}.Cube:nth-child(6){-webkit-animation-delay:7.5s;animation-delay:7.5s;left:30vw;top:10vh}@-webkit-keyframes cube{from{transform:scale(0) rotate(0deg) translate(-50%, -50%);opacity:1}to{transform:scale(20) rotate(960deg) translate(-50%, -50%);opacity:0}}@keyframes cube{from{transform:scale(0) rotate(0deg) translate(-50%, -50%);opacity:1}to{transform:scale(20) rotate(960deg) translate(-50%, -50%);opacity:0}}

body{background-color:#e7efec}.App{position:relative;display:flex;flex-direction:column;width:100%;height:100vh}.App-Header.header:first-child{margin-top:20px}.App-Footer{display:flex;justify-content:center;align-items:center;flex-shrink:0;height:60px;background-color:#c7e2f6}

